import React, {useEffect, useState} from "react";
import { useParams } from "react-router";
import { axiosInstance } from "../api/axiosInstance";
import SkeletonCard from "../components/skeleton";

const Search = () =>{
    const params = useParams();
    const [data, setData] = useState([]);
    const [api, setApi] = useState(false)
    useEffect(()=>{
        axiosInstance.get(`api/v1/videos/search/${params.keyword}`).then((res)=>{
            setData(res.data.data)
            setApi(true)
        }).catch((err)=>{
            console.log(err)
        })
    }, [])
    if(!localStorage.getItem("token")){
        return window.location.assign("/login")
      }
    return (
        <>
            <div style={{minHeight: 500}}>
                <div className='w-11/12 mx-auto'>
                    <div className='text-xl font-bold text-gray-600 pt-4'>Search result for {params.keyword}</div>
                    <div className='grid grid-cols-2 sm:grdi-cols-3 md:grid-cols-4 lg:grid-cols-6 gap-1 gap-y-6 my-4'>
                        {
                           api ? data.map((item, index)=>{
                                return <div key={index} className='w-full shadow-lg border rounded'>
                                    <div className="overflow-hidden "><div className='w-full transition duration-300 ease-in-out transform hover:scale-110  h-28 bg-cover' style={{backgroundImage:  `url(${data && data[0].thumbnail})`}}></div></div>
    
                                <div className=' w-full px-2 pt-1  text-gray-800 font-semibold'>{item.title}</div>
                                <div className="flex  px-2 text-sm mb-2 text-gray-500"><svg className="w-5 h-5 salf-center" fill="currentColor" viewBox="0 0 20 20" xmlns="http://www.w3.org/2000/svg"><path fillRule="evenodd" d="M5.05 4.05a7 7 0 119.9 9.9L10 18.9l-4.95-4.95a7 7 0 010-9.9zM10 11a2 2 0 100-4 2 2 0 000 4z" clipRule="evenodd" /></svg> <div>Location</div></div>
                            </div>
                            }) : <SkeletonCard />
                        }
                    </div>
                </div>
            </div>
        </>
    )
}

export default Search;