import React from "react";
import FacebookIcon from '@mui/icons-material/Facebook';
import TwitterIcon from '@mui/icons-material/Twitter';
import LinkedInIcon from '@mui/icons-material/LinkedIn';
import YouTubeIcon from '@mui/icons-material/YouTube';
 
const Footer = () =>{
    return (
        <>
            <div className="bg-gray-800 w-full">
                <div className="flex flex-col gap-2 w-72 items-center justify-center mx-auto pt-10">
                    <div className="text-2xl font-bold text-white">Meta Tours</div>
                    <div className="text-center text-gray-400 text-sm font-semibold">
                    Travel in virtuality, Enjoy in reality
                    </div>
                    <div className="flex gap-2">
                        <FacebookIcon className="text-gray-100" />
                        <TwitterIcon className="text-gray-100" />
                        <LinkedInIcon className="text-gray-100" />
                        <YouTubeIcon className="text-gray-100" />
                    </div>
                </div>
                    <div className="bg-gray-900 py-2 text-gray-500 mt-4 text-center text-sm ">
                        Copyright @2021 Meta Tours.
                    </div>
            </div>
        </>
    )
}
 
export default Footer;