import React, { useEffect, useState } from "react";
import HomeCarousel from "../components/HomeCarousel";
import { Link } from "react-router-dom";
import SkeletonCard from "../components/skeleton";
import { axiosInstance } from "../api/axiosInstance";
import img1 from "../assist/1.jpg"

const videosList = [
    {
        id: 1,
        url: "https://www.youtube.com/watch?v=WsMjBMxpUTc",
        image: img1,
        title: "",
        location: "",
        description: ""
    }
]

const Home = () =>{
    const [api, setApi] = useState(false);
    const [data, setData] = useState([]);

    useEffect(()=>{
        // 
         axiosInstance.get("api/v1/videos/list").then((res)=>{
             console.log(res)
             setData(res.data.data)
             setApi(true)
            }).catch((err)=>{
             console.log(err)
        })
    }, [])

    if(!localStorage.getItem("token")){
        return window.location.assign("/login")
      }
    return (
        <div>
            <HomeCarousel />
            <div className="mb-10">
            <div className='w-11/12 mx-auto'>
                <div className='text-xl font-bold text-gray-600 pt-4'>Top videos</div>
                <div className='grid grid-cols-2 sm:grdi-cols-3 md:grid-cols-4 lg:grid-cols-5 gap-3 gap-y-6 my-2'>
                    {
                        api ? data.map((item, index)=>{
                            if(index<5){
                                return <Link to={`/videos/${item.id}`} key={index} className='w-full shadow-lg border rounded'>
                                <div className="overflow-hidden "><div className='w-full transition duration-300 bg-cover ease-in-out transform hover:scale-110  h-28' style={{backgroundImage:  `url(${item.thumbnail})`}}></div></div>

                            <div className=' w-full px-2 pt-1  text-gray-800 font-semibold'>{item.title}</div>
                            <div className="flex  px-2 mb-2 text-sm text-gray-500"><svg className="w-5 h-5 salf-center" fill="currentColor" viewBox="0 0 20 20" xmlns="http://www.w3.org/2000/svg"><path fillRule="evenodd" d="M5.05 4.05a7 7 0 119.9 9.9L10 18.9l-4.95-4.95a7 7 0 010-9.9zM10 11a2 2 0 100-4 2 2 0 000 4z" clipRule="evenodd" /></svg> <div>Location</div></div>
                        </Link>
                            }
                            return null
                        }) : <SkeletonCard />
                    }
                </div>
            </div>
 
 
            <div className='w-11/12 mx-auto'>
                <div className='text-xl font-bold text-gray-600 pt-4'>Videos you way like</div>
                <div className='grid grid-cols-2   sm:grdi-cols-3 md:grid-cols-4 lg:grid-cols-5 gap-3 gap-y-6 my-2'>
                {
                        api ? data.map((item, index)=>{
                            if(index>5){
                                return <Link to={`/videos/${item.id}`} key={index} className='w-full shadow-lg border rounded'>
                                <div className="overflow-hidden "><div className='w-full transition duration-300 ease-in-out transform hover:scale-110 bg-cover  h-28' style={{backgroundImage:  `url(${item.thumbnail})`}}></div></div>

                            <div className=' w-full px-2 pt-1  text-gray-800 font-semibold'>{item.title}</div>
                            <div className="flex  px-2 mb-2 text-sm text-gray-500"><svg className="w-5 h-5 salf-center" fill="currentColor" viewBox="0 0 20 20" xmlns="http://www.w3.org/2000/svg"><path fillRule="evenodd" d="M5.05 4.05a7 7 0 119.9 9.9L10 18.9l-4.95-4.95a7 7 0 010-9.9zM10 11a2 2 0 100-4 2 2 0 000 4z" clipRule="evenodd" /></svg> <div>Location</div></div>
                        </Link>
                            }
                            return null;
                            
                        }) : <SkeletonCard />
                    }
                </div>
            </div>
            </div>
            
        </div>
    )
}

export default Home;