import React from "react";
// import LoginIcon from "@mui/icons-material/Login";
import { Link } from "react-router-dom";
 
const GetStarted = () => {
  if(localStorage.getItem("token")){
    return window.location.assign("/")
  }
  return (
    <div className="w-screen h-screen">
      <div
        className="w-full h-full bg-cover "
        style={{
          backgroundImage: `url(https://www.dishanews.com/wp-content/uploads/2021/07/Use-of-Beacon-Technology-in-Travel-and-Tourism-Industry.png)`,
          backgroundRepeat: "no-repeat",
        }}
      >
        <div className="h-full flex flex-col w-full bg-gradient-to-t pt-6 from-green-200 via-transparent to-transparent bg-opacity-25">
          <div className="flex justify-between w-11/12 mx-auto">
            <div className="text-yellow-600 text-4xl font-bold">Logo</div>
            <Link
              to="/login"
              className="bg-red-500 flex justify-center hover:bg-red-600 shadow-md w-28 py-1  font-semibold text-center self-center rounded-full text-white"
            >
              <div>Sign in</div>
              <div>
                {/* <LoginIcon /> */}
              </div>
            </Link>
          </div>
          <div className="w-full flex-1 flex justify-center  flex-col items-center pt-20">
            <div className="w-full mx-auto">
              <div
                style={{maxWidth: 500}}
                className="text-xl sm:text-5xl mx-auto  text-center font-bold text-red-600 "
              >
                Travel in virtuality, Enjoy in reality
              </div>
              <div className="text-md sm:text-xl w-80 sm:w-96 mx-auto text-center font-medium text-gray-900">
                Watch anywhere. Cancel anytime.
              </div>
              <br />
              <div className="text-sm w-72 sm:auto mx-auto text-center font-semibold mb-2 sm:font-bold text-gray-900">
                Ready to watch? Enter your email to create or restart your
                membership.
              </div>
            </div>
            <div className="flex w-11/12 sm:w-10/12 md:w-1/2 lg:w-5/12 mx-auto shadow-lg rounded overflow-hidden">
              <input
                className="w-7/12 sm:w-8/12  lg:w-9/12 p-2 font-xl border font-semibold focus:outline-none"
                type="text"
                placeholder="Email address"
              />
              <button className="w-5/12 sm:w-4/12 lg:w-3/12 py-2 flex justify-center items-center bg-red-500 hover:bg-red-600 text-white text-xl font-semibold">
                <div className="self-center pb-1">Get Started</div>
                <svg
                  className="w-6 h-6"
                  fill="currentColor"
                  viewBox="0 0 20 20"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    fillRule="evenodd"
                    d="M7.293 14.707a1 1 0 010-1.414L10.586 10 7.293 6.707a1 1 0 011.414-1.414l4 4a1 1 0 010 1.414l-4 4a1 1 0 01-1.414 0z"
                    clipRule="evenodd"
                  />
                </svg>
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
 
export default GetStarted;