import GetStarted from './pages/GetStarted';
import './App.css';
import {BrowserRouter as Router, Switch, Route } from "react-router-dom"
import Login from './pages/Login';
import Home from './pages/Home';
import Videos from './pages/Videos';
import Navbar from './static/Navbar';
import Footer from './static/Footer';
import Search from "./pages/Search"

function App() {
  return (
    <div>
      <Router>
      {window.location.pathname !== "/getstart" && window.location.pathname !== "/login" && <Navbar />}
        <Switch>
          <Route path="/getstart" component={GetStarted} exact />
          <Route path="/login" component={Login} exact />
          <Route path="/" component={Home} exact />
          <Route path="/videos/:id" component={Videos} exact />
          <Route path="/search/:keyword" component={Search} exact />
        </Switch>
      </Router>
      {window.location.pathname !== "/getstart" && window.location.pathname !== "/login" && <Footer />}
    </div>
  );
}

export default App;
