import { useEffect, useState } from "react";
import { useParams } from "react-router";
import React from "react";
import SkeletonCard from "../components/skeleton";
import { axiosInstance } from "../api/axiosInstance";
import { Link } from "react-router-dom";

const Videos = () =>{
    const [api, setApi] = useState(false)

    const [videoData, setVideoData] = useState([]);
    const [data, setData] = useState([]);

    const [id,setId] = useState("")

    const params = useParams();

    useEffect(()=>{
        if(id){
            axiosInstance.get(`api/v1/videos/${id}`).then((res)=>{
                console.log(res.data.data)
                setVideoData(res.data.data)
            })
        }
    }, [id])

    useEffect(()=>{
        window.scrollTo(0, 0);
        // window.setTimeout(()=>{setApi(true)}, 2000)
        axiosInstance.get("api/v1/videos/list").then((res)=>{
            console.log(res.data)
            setData(res.data.data)
            setApi(true);
        }).catch((err)=>{
            console.log(err)
        })
        axiosInstance.get(`api/v1/videos/${params.id}`).then((res)=>{
            console.log(res.data.data)
            setVideoData(res.data.data)
        })
    }, [])
    
    if(!localStorage.getItem("token")){
        return window.location.assign("/login")
      }
    return (
        <div style={{minHeight: 500}} className="mb-4">
            <div className="mt-5 w-11/12 flex flex-col sm:flex-row gap-6 mx-auto">
                {/* <ReactPlayer className="w-9/12" controls speed volume url='http://10.10.11.129:3000/api/v1/videos/stream/sample-30s.mp4' playing /> */}
                {/* <video className="w-8/12 h-96" controls>
                    <source src="http://10.10.11.129:3000/api/v1/videos/stream/vr-video.mp4" type="video/mp4" />
                    Your browser does not support the video tag.
                </video> */}
                {/* <VrPlayer
                sources={sources}
                brand="Some Brand Name"
                title="Some Video Title"
                keys={keys} /> */}
                <div className="w-full sm:w-8/12 ">
                 <iframe className="w-full h-96" src={videoData && videoData[0] && videoData[0].url} title="YouTube video player" frameBorder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>
                 <div className="mt-2">
                     <div className="font-bold text-2xl text-gray-800 ">{videoData && videoData[0] && videoData[0].title}</div>
                     <div className="flex pt-1 text-sm text-gray-500"><svg className="w-5 h-5 salf-center" fill="currentColor" viewBox="0 0 20 20" xmlns="http://www.w3.org/2000/svg"><path fillRule="evenodd" d="M5.05 4.05a7 7 0 119.9 9.9L10 18.9l-4.95-4.95a7 7 0 010-9.9zM10 11a2 2 0 100-4 2 2 0 000 4z" clipRule="evenodd" /></svg> <div>Location</div></div>
                 </div>
                 
                </div>
            
                <div className='w-full sm:w-4/12 mx-auto'>
                <div className='text-xl font-bold text-gray-600'>Videos you may like</div>
                <div className='grid grid-cols-2 gap-1 gap-y-6 my-2'>
                    {
                       api ? data.map((item, index)=>{
                            return (<Link to={`/videos/${item.id}`} onClick={()=>setId(item.id)} key={index} className='w-full shadow-lg border rounded'>
                            <div className='w-full h-28 bg-cover' style={{backgroundImage: `url(${item.thumbnail})`}}></div>
                            <div className=' w-full px-2 pt-1  text-gray-800 font-semibold'>{item.title}</div>
                            <div className="flex  px-2 text-sm mb-2 text-gray-500"><svg className="w-5 h-5 salf-center" fill="currentColor" viewBox="0 0 20 20" xmlns="http://www.w3.org/2000/svg"><path fillRule="evenodd" d="M5.05 4.05a7 7 0 119.9 9.9L10 18.9l-4.95-4.95a7 7 0 010-9.9zM10 11a2 2 0 100-4 2 2 0 000 4z" clipRule="evenodd" /></svg> <div>Location</div></div>
                        </Link>)
                        }) : <SkeletonCard />
                    }
                </div>
            </div>
            </div>
        </div>
    )
}

export default Videos;