import React from "react";
import Carousel from "react-material-ui-carousel";

const HomeCarousel = () => {
  var items = [
    {
      name: "Mustang",
      description: "Probably the most random thing you have ever seen!",
      image:
        "http://www.trekkingsikkim.com/assets/images/nepal/trekking-in-nepal/mustang-trek-.jpg",
    },
    {
      name: "Pokhara",
      description: "Probably the most random thing you have ever seen!",
      image:
        "https://pix10.agoda.net/hotelImages/412/4122185/4122185_18010620130060843922.jpg?s=1024x768",
    },
  ];
  return (
    <>
      <Carousel PrevIcon={<svg className="w-6 h-6" fill="white" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 19l-7-7 7-7" /></svg>} NextIcon={<svg className="w-6 h-6" fill="white" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 5l7 7-7 7" /></svg>} style={{ height: "500px" }}>
        {items.map((item, i) => (
          <Item key={i} item={item} />
        ))}
      </Carousel>
    </>
  );
};

export default HomeCarousel;

function Item(props) {
  return (
    <div
      style={{
        backgroundImage: `url(${props.item.image})`,
        height: "500px",
        backgroundRepeat: "no-repeat",
      }}
      className="w-full bg-cover  bg-opacity-75 "
    >
      <div className="flex justify-center items-center sm:items-end sm:pr-20 flex-col gap-2 bg-gradient-to-b from-transparent  to-gray-900  w-full h-full">
        <div className="flex justify-center w-96 items-center flex-col gap-2">
          <h2 className="text-4xl font-bold text-yellow-600">
            {props.item.name}
          </h2>
          <p className="text-white w-72 sm:w-auto text-xl font-semibold text-center">
            {props.item.description}
          </p>
          <button className="bg-yellow-600  hover:bg-yellow-500 px-4 p-1 pb-2 text-xl text-white font-semibold rounded">
            Watch now
          </button>
        </div>
      </div>
    </div>
  );
}
