import React from "react";

const SkeletonCard = () =>{
    return (
        <>
            {
                        [1,2,3,4,5].map((item)=>{
                            return <div key={item} className='w-full animate-pulse shadow-lg border rounded '>
                                <div className="overflow-hidden "><div className='w-full bg-gray-300 transition duration-300 ease-in-out transform hover:scale-110  h-28'></div></div>

                            <div className=' w-20 bg-gray-500 px-2 pt-1 h-4 mt-2 text-gray-800 font-semibold ml-2'></div>
                            <div className="flex w-14 bg-gray-500 px-2 mb-2 mt-1 h-3 text-sm text-gray-500 ml-2"><div></div></div>
                        </div>
                        })
                    }
        </>
    )
}

export default SkeletonCard;