import React, {useState} from "react";
import { Link } from "react-router-dom";
 
const Navbar = () => {
  const [keyword, setKeyword] = useState("");
  const [isLogout, setIsLogout] = useState(false)

  const handleLogout = () =>{
    localStorage.clear();
    setIsLogout(true);
  }

  if(isLogout){
    return window.location.assign("/login")
  }

  return (
    <div >
      <ul className="h-14 bg-gray-800 flex px-2 sm:px-10 justify-between items-center text-white gap-3 sm:gap-6">
        <Link to="/" className="text-lg sm:text-2xl font-semibold py-2 sm:py-0 sm:font-bold">Meta Tours</Link>
        <li className="w-1/2  flex justify-center">
          <input
            onChange={(e)=>setKeyword(e.target.value)}
            value={keyword}
            className=" focus:outline-none font-medium bg-opacity-70 focus:bg-gray-700 transision-300 w-11/12 bg-gray-500 py-1 px-4 rounded-l"
            type="text"
            placeholder="search"
          />
          <Link to={keyword && `/search/${keyword}`} className="bg-yellow-500 bg-opacity-70 px-2 pt-1 rounded-r">
            <svg
              className="w-6 h-6"
              fill="none"
              stroke="currentColor"
              viewBox="0 0 24 24"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth={2}
                d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z"
              />
            </svg>
          </Link>
        </li>
        <li className="text-lg font-semibold flex gap-2 sm:gap-6">
          <div>
            <svg
              className="w-7 h-7"
              fill="currentColor"
              viewBox="0 0 20 20"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                fillRule="evenodd"
                d="M18 10a8 8 0 11-16 0 8 8 0 0116 0zm-6-3a2 2 0 11-4 0 2 2 0 014 0zm-2 4a5 5 0 00-4.546 2.916A5.986 5.986 0 0010 16a5.986 5.986 0 004.546-2.084A5 5 0 0010 11z"
                clipRule="evenodd"
              />
            </svg>
          </div>
          <button onClick={()=>handleLogout()}>
            <svg
              className="w-7 h-7"
              fill="currentColor"
              viewBox="0 0 20 20"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                fillRule="evenodd"
                d="M3 3a1 1 0 00-1 1v12a1 1 0 102 0V4a1 1 0 00-1-1zm10.293 9.293a1 1 0 001.414 1.414l3-3a1 1 0 000-1.414l-3-3a1 1 0 10-1.414 1.414L14.586 9H7a1 1 0 100 2h7.586l-1.293 1.293z"
                clipRule="evenodd"
              />
            </svg>
          </button>
        </li>
      </ul>
    </div>
  );
};
 
export default Navbar;